import React, { useContext, useState } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
  MenuItem,
  Radio,
  RadioGroup,
  FormControlLabel,
} from "@mui/material";
import NigerianStates from "../NigerianStates";
import axios from "axios";
import baseUrl from "../../Route/BaseURL";
import { ContextData } from "../context/Context";
import PopUpBeforePayment from "./PopUpBeforePayment";

// pk_test_9ae62dd11febf506958542f1f1f5a06089620603

function PopupDialog() {
 
  
  const {accesstoken,CLosePayPOp,setCLosePayPOp} = useContext(ContextData)
  const [selectedSection, setSelectedSection] = useState("ticket"); // 'ticket' or 'stall'
  const [Tickets, setTickets] = React.useState(null);


   
  React.useEffect(() => {


    async function FetchData() {
     
      try {
        const response = await axios.get(`${baseUrl}/auth/get_ticket`,{
          withCredentials: true,
          headers: {
            'Accept': "application/json",
           'authorization': `Bearer ${accesstoken}`
          },
        });
       
        setTickets(response.data);
      
      } catch (err) {
        if (err) console.log(err.message);
        
      }
    }
    FetchData();


  }, []);



  // State for Buy Ticket form
  const [formData, setFormData] = useState({

    fullName: "",
    mobileNumber: "",
    state: "Kaduna",
    ticketType: "",

    businessName: "null",
    stallPrice: "null"


  });

  // State for Book Stall form
  const [stallFormData, setStallFormData] = useState({

    fullName: "",
    state: "Kaduna",
    businessName: "",
    mobileNumber: "",
    stallPrice: "", // Fixed price, displayed but not editable

  });



  const handleClickOpen = () => {
    setCLosePayPOp(true);
  };

  const handleClose = () => {
    setCLosePayPOp(false);
  };

  // Handle input changes for both forms based on the selected section
  const handleChange = (e) => {

    const { name, value } = e.target;
    if (selectedSection === "ticket") {
      setFormData((prevData) => ({
        ...prevData,
        [name]: value,
      }));
    } else {
      setStallFormData((prevData) => ({
        ...prevData,
        [name]: value,
      }));
    }
  };

  const handleSubmit = () => {



    

    if (selectedSection === "ticket") {
      console.log("Ticket Form Submitted", formData);
    } else {
      console.log("Stall Form Submitted", stallFormData);
    }
    handleClose();


 





  };




  return (
    <div id="popgetticket">
      <div className="action-button-container">
        <Button
          variant="contained"
          onClick={handleClickOpen}
          style={{
            background: `linear-gradient(10deg,blue,rgb(23, 1, 23))`,
            color: "white",
            marginTop: "20px",
          }}
          className="book-btn"
        >
          Buy ticket or Book a stall
        </Button>
      </div>

      <Dialog id="popgetticket" open={CLosePayPOp} onClose={handleClose}>
        <DialogTitle>Buy Ticket</DialogTitle>

        <DialogContent>
          {/* Radio buttons for selecting section */}
          <RadioGroup
            row
            value={selectedSection}
            onChange={(e) => setSelectedSection(e.target.value)}
            className="mb-4"
          >
            <FormControlLabel
              value="ticket"
              control={<Radio />}
              label="Buy Ticket"
            />
            <FormControlLabel
              value="stall"
              control={<Radio />}
              label="Book A Stall"
            />
          </RadioGroup>

          {/* Conditional rendering based on selectedSection */}

          {selectedSection === "ticket" ? (
            <>
              <TextField
                margin="dense"
                label="Full Name"
                name="fullName"
                value={formData.fullName}
                onChange={handleChange}
                fullWidth
                required
              />

              
                <p>
                   <h4 className="text-center font-extrabold pt-2">Attention!!!</h4>
                   <h5 className="w-full p-4">
  <p className="text-justify">
    Please provide a valid contact number. You will receive your ticket ID via this number (SMS). If you don't receive it, you can also use your contact number to download your ticket in the section below, after the event details.
     <p> Additionally, we can use your registered contact number to verify your entry at the gate on the day of the event. The choice is yours.</p>
  </p>
</h5>
                </p>
              <TextField
                margin="dense"
                 label="Phone Number -> must be 11 digit Nigerian format"
                name="mobileNumber"
                value={formData.mobileNumber}
                onChange={handleChange}
                fullWidth
                type="tel"  // Use "tel" for better phone input handling
                inputProps={{ maxLength: 11 }}  // Limits to 10 characters
              />
              <TextField
                select
                margin="dense"
                label="State of Origin"
                name="state"
                value={formData.state}
                onChange={handleChange}
                fullWidth
                required
              >
                {NigerianStates.map((state, index) => (
                  <MenuItem key={index} value={state}>
                    {state}
                  </MenuItem>
                ))}
              </TextField>
              <TextField
                select
                margin="dense"
                label="Ticket Type"
                name="ticketType"
                value={formData.ticketType}
                onChange={handleChange}
                fullWidth
                required
              >
                {Tickets !== null && Tickets.map((ticket,id) => (
                  <MenuItem  disabled={ticket.ticket_type === "stall" ? true : false} key={id} value={`${ticket.ticket_type}-${ticket.price}`}>
                    {ticket.ticket_type} - {ticket.price.toLocaleString()}
                  </MenuItem>
                ))}
              </TextField>
            </>
          ) : (
            <>
              <TextField
                margin="dense"
                label="Full Name"
                name="fullName"
                value={stallFormData.fullName}
                onChange={handleChange}
                fullWidth
                required
              />
               <TextField
                select
                margin="dense"
                label="State of Origin"
                name="state"
                value={formData.state}
                onChange={handleChange}
                fullWidth
                required
              >
                {NigerianStates.map((state, index) => (
                  <MenuItem key={index} value={state}>
                    {state}
                  </MenuItem>
                ))}
              </TextField>

              <TextField
                margin="dense"
                label="Business Name"
                name="businessName"
                value={stallFormData.businessName}
                onChange={handleChange}
                fullWidth
                required
                inputProps={{ maxLength: 50 }}  // Limits to 100 characters
              />
                 <h4 className="text-center font-extrabold pt-2">Attention!!!</h4>
                <h5 className="p-4"><p className="text-justify"> "Please provide a valid contact number. One of our team members will reach out to you as soon as we receive your payment."</p></h5>
              
              <TextField
                margin="dense"
                label="Phone Number -> must be 11 digit Nigerian format"
                name="mobileNumber"
                value={stallFormData.mobileNumber}
                onChange={handleChange}
                fullWidth
                required
                type="tel"  // Use "tel" for better phone input handling
                inputProps={{ maxLength: 11 }}  // Limits to 10 characters
              /> 
 
              <TextField
                select
                margin="dense"
                label="Select Payment"
                name="stallPrice"
                value={stallFormData.stallPrice}
                onChange={handleChange}
                fullWidth
                required
                >
                {Tickets !== null && Tickets.map((ticket,id) => (
                  <MenuItem disabled={ticket.ticket_type === "stall" ? false : true} key={id} value={`${ticket.ticket_type}-${ticket.price}`}>
                    {ticket.ticket_type} - {ticket.price.toLocaleString()}
                  </MenuItem>
                ))}
              </TextField>

              <TextField
                margin="dense"
                label="Stall Fee"
                value={stallFormData.stallPrice.slice(stallFormData.stallPrice.lastIndexOf("-") + 1)}
                disabled
                fullWidth
              />

             

            </>
          )}
        </DialogContent>
        <DialogActions>

          <div className="flex items-center content-center">

          <Button onClick={handleClose} color="secondary">
            Cancel
          </Button>

           <PopUpBeforePayment data={selectedSection === "ticket" ? formData : stallFormData } ticketPayment={selectedSection} />

          </div>


        </DialogActions>
      </Dialog>
    </div>
  );
}

export default PopupDialog;
