import React, { useState, useEffect } from 'react';
import { useTransition, animated, useSpringRef } from '@react-spring/web';

import styles from './style.module.css';

const pages = [
  (props) => <animated.div className="ani-box1" style={{ ...props.style }}>MastermindTech</animated.div>,
  (props) => <animated.div className="ani-box2" style={{ ...props.style }}> Contact us today at 07040768898 / WhatsApp US</animated.div>,
  (props) => <animated.div className="ani-box3" style={{ ...props.style }}>we specialize in developing innovative software solutions tailored to meet your unique business needs. </animated.div>,
];

export default function MMT() {
  const [index, set] = useState(0);
  const onClick = () => set((state) => (state + 1) % 3);

  useEffect(() => {
    const timer = setInterval(() => {
      set((state) => (state + 1) % 3);
    }, 5000);
    return () => clearInterval(timer);
  }, []);

  const transRef = useSpringRef();
  const transitions = useTransition(index, {
    ref: transRef,
    keys: null,
    from: { opacity: 0, transform: 'translate3d(100%,0,0)' },
    enter: { opacity: 1, transform: 'translate3d(0%,0,0)' },
    leave: { opacity: 0, transform: 'translate3d(-50%,0,0)' },
  });

  useEffect(() => {
    transRef.start();
  }, [index]);

  return (
    <div className={`flex fill ${styles.container}`} onClick={onClick}>
      {transitions((style, i) => {
        const Page = pages[i];
        return <Page style={style} />;
      })}
    </div>
  );
}