import React, { useContext, useState } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle
} from "@mui/material";
import NigerianStates from "../NigerianStates";
import axios from "axios";
import baseUrl from "../../Route/BaseURL";
import { ContextData } from "../context/Context";
import { toast } from "react-toastify";
import { usePaystackPayment } from 'react-paystack';
import AlertPOP from "../../Route/AlertPOP";

// pk_test_9ae62dd11febf506958542f1f1f5a06089620603

//pk_live_d447da16423eac4a75643ce28d7546377a836f6d

const config = {
  publicKey: 'pk_live_9fb9da914b757f1bb1dc9590ef8e057764886a6c',
} 






function PopUpBeforePayment(props) {
 
  
  const {accesstoken,setFadeConfetti,setCLosePayPOp} = useContext(ContextData)
  const [open, setOpen] = useState(false);
  const [selectedSection, setSelectedSection] = useState("ticket"); // 'ticket' or 'stall'
  const [Tickets, setTickets] = React.useState(null);
  const point = "00"
 

  const [message, setmessage] = useState("");
  const [openAlert, setOpenAlert] = useState(false);
  const [transition, setTransition] = useState(undefined);

  // Handle the alert transition and function
  const handleClick = (Transition) => {
    setTransition(() => "TransitionLeft");
    setOpenAlert(true);
  };
  // Handle the close alert box
  const handleCloseAlert = () => {
    setOpenAlert(false);
  };
   
  React.useEffect(() => {


    async function FetchData() {
     
      try {
        const response = await axios.get(`${baseUrl}/auth/get_ticket`,{
          withCredentials: true,
          headers: {
            'Accept': "application/json",
           'authorization': `Bearer ${accesstoken}`
          },
        });
       
        setTickets(response.data);
      
      } catch (err) {
        if (err) console.log(err.message);
        
      }
    }
    FetchData();


  }, []);


         
  const initializePayment = usePaystackPayment({...config,


     
    reference: (new Date()).getTime().toString(),
    phone: props.data.mobileNumber,
    email:`master${Math.floor(Math.random() * 649428)}tech.work@gmail.com`,
    amount: props.ticketPayment === "ticket" ?  Number(props.data.ticketType.slice(props.data.ticketType.lastIndexOf("-")+1)+point) :  Number(props.data.stallPrice.slice(props.data.stallPrice.lastIndexOf("-") + 1)+point),
    firstname:  props.data.fullName


     });
     
     
  


  const handleClickOpen = () => {


       
    const isEmptyField = Object.values(props.data).some((value) => value === "");

   
    if (isEmptyField) {
      toast.warn("Please fill out all fields");
      return;
    }

       setOpen(true)

  };

  const handleClose = () => {
 
    setOpen(false);
    
  };



 const Paynow = ()=>{

  
  axios.post(`${baseUrl}/pub/check_number`, {...props.data}, {
    withCredentials: true,
    headers: {
      Accept: "application/json",
      authorization: `Bearer ${accesstoken}`,  
    }, 
  })
  .then((res) => {

    if (res.data.status) {

      setmessage(res.data.statusText);
      toast.success(res.data.statusText);
      setOpen(false);
     

       setTimeout(()=>{
             // Excute Payment fuction
             initializePayment(onSuccess,onClose) 
       },2000)
    
    }else{

      setmessage(res.data.statusText);
      toast.warn(res.data.statusText);
      setOpen(false);

    }


  })
  .catch((err) => {
    toast.warn(err.message);
    setmessage(err.message);
    if (err) return console.log(err);
  });
 
    

  } 


  const onSuccess = (reference) => {
     
      
      let tpy = props.ticketPayment === "ticket" ? props.ticketPayment : props.ticketPayment
      let ticketfee = props.ticketPayment === "ticket" &&  props.data.ticketType.slice(props.data.ticketType.lastIndexOf("-")+1)
      
      setFadeConfetti(true)
      // Call Alert Box function
       handleClick("left");
       const timer = setTimeout(()=>{
          setFadeConfetti(null)
        clearTimeout(timer)
      },17000)

      axios
      .post(`${baseUrl}/pub/add_audiance`, {...props.data,paid_for : tpy  ,ownertype:"to verify", ticketfee: props.ticketPayment === "ticket" ? ticketfee : "stallfee",reference:reference }, {
        withCredentials: true,
        headers: {
          Accept: "application/json",
          authorization: `Bearer ${accesstoken}`,  
        }, 
      })
      .then((res) => {
 
        if (res.data.status) {

          setmessage(res.data.statusText);
          toast.success(res.data.statusText);
          setOpen(false);
          setCLosePayPOp(false)
            
           const timer =  setTimeout(()=>{

            toast.success(
              "Download your ticket using Ticket ID or your registered contact number below after Event Details Section", 
              {
                autoClose: 10000, // Time in milliseconds (e.g., 8000ms = 8 seconds)
                pauseOnHover: true, // Keeps the toast open when the user hovers over it
              }
            );

            clearTimeout(timer)

          },4000)

          
        }else{

          setmessage(res.data.statusText);
          toast.warn(res.data.statusText);
          setOpen(false);

        }


      })
      .catch((err) => {
        toast.warn(err.message);
        setmessage(err.message);
        if (err) return console.log(err);
      });

      


  };
  
  const onClose = () => {
    // implementation for  whatever you want to do when the Paystack dialog closed.
    console.log('closed')
    
  }




  return (
    <div id="popgetticket">
     
       
          <Button onClick={handleClickOpen} color="primary" variant="contained">
            Proceed
          </Button>
    
        

      <Dialog id="popgetticket" open={open} onClose={handleClose}>
        <DialogTitle sx={{textTransform:"capitalize"}}>  {props.ticketPayment} Payment</DialogTitle>

        <DialogContent>
        <AlertPOP
          Open={openAlert}
          handleClose={handleCloseAlert}
          transition={transition}
          message={message}
        />
        <div style={{width:'100%',height:'19em'}}>
            <p style={{paddingTop:'6px' , textAlign:"justify"}}> 
           
            <p className="mb-3">
            By clicking the Payment button, you will be redirected to the secure payment pop up page, where you can securely enter your payment details and complete the registration. 
             
            </p>

            <p className="mb-3">
            We have implemented industry-standard security measures to ensure the safety and privacy of your information.
            </p>

            <p>

            If you encounter any issues during the payment process or have any questions, please do not hesitate to contact our customer support team at 07040768898.
            </p>
          </p>
         </div> 
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="secondary">
            Cancel
          </Button>
          <Button  onClick={Paynow} color="primary" variant="contained">
            Pay now
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default PopUpBeforePayment;
