import * as React from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import TextField from "@mui/material/TextField";
import MenuItem from "@mui/material/MenuItem";
import { useState } from "react";
import axios from "axios";
import baseUrl from "../../../Route/BaseURL";
import AlertPOP from "../../../Route/AlertPOP";
import { ContextData } from "../../context/Context";
import { toast } from "react-toastify";
import NigerianStates from "../../NigerianStates";

export default function AddAttendeeDialog() {


  const [open, setOpen] = React.useState(false);
  const { RefreshUesFF, setRefreshUesFF, accesstoken } = React.useContext(ContextData);
  const [Tickets, setTickets] = React.useState(null);



  React.useEffect(() => {

    async function FetchData() {
     
      try {
        const response = await axios.get(`${baseUrl}/auth/get_ticket`, {
          withCredentials: true,
          headers: {
            'Accept': "application/json",
           'authorization': `Bearer ${accesstoken}`
          },
        });
       
        setTickets(response.data);
      
      } catch (err) {
        if (err) console.log(err.message);
        
      }
    }
    FetchData();

  }, []);



  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  //Form start---------->

  const [formData, setFormData] = useState({
    fullName: "",
    state: "",
    mobileNumber: "",
    ticketType: "",
    ownertype:"free ticket",
    paid_for: "free ticket"
  });

  const handleInputChange = (field) => (e) => {
    setFormData({ ...formData, [field]: e.target.value });
  };

  const [message, setmessage] = useState("");
  const [openAlert, setOpenAlert] = useState(false);
  const [transition, setTransition] = useState(undefined);

  // Handle the alert transition and function
  const handleClick = (Transition) => {
    setTransition(() => "TransitionLeft");
    setOpenAlert(true);
  };
  // Handle the close alert box
  const handleCloseAlert = () => {
    setOpenAlert(false);
  };

  // Handle submit
  const handleSubmit = (e) => {
    e.preventDefault();

    // Call Alert Box function
    handleClick("left");

    // Check for empty fields
    const isEmptyField = Object.values(formData).some((value) => value === "");

    if (isEmptyField) {
      setmessage("Please fill out all fields");
      return;
    }

    // Submit the form (you can implement your submission logic here)

    //  console.log(formData)

    axios
      .post(`${baseUrl}/auth/add_audiance`, {...formData,fromwho:"dash"}, {
        withCredentials: true,
        headers: {
          Accept: "application/json",
          authorization: `Bearer ${accesstoken}`,
        },
      })
      .then((res) => {
        if (res.data.status) {
          setmessage(res.data.statusText);
          toast.success(res.data.statusText);
          setRefreshUesFF(!RefreshUesFF);

          setFormData({
            fullName: "",
            state: "",
            mobileNumber: "",
            ticketType: "",
            ownertype:"",
             paid_for: ""
          });
        }

        if (!res.data.status) {
          setmessage(res.data.statusText);
          toast.warn(res.data.statusText);
        }
      })
      .catch((err) => {
        if (err) return console.log(err);
      });
  };


  //Form end --------->

  return (
    <React.Fragment>
      <Button
        variant="contained"
        onClick={handleClickOpen}
        className="admin-users_header-btn"
      >
        Add Attendee
      </Button>
      <Dialog
        open={open}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        className="py-10"
      >
        <AlertPOP
          Open={openAlert}
          handleClose={handleCloseAlert}
          transition={transition}
          message={message}
        />
        <DialogTitle id="alert-dialog-title">New Attendee</DialogTitle>
        <DialogContent>
          <form onSubmit={handleSubmit}>
            <TextField
              label="Full Name"
              variant="outlined"
              fullWidth
              margin="normal"
              value={formData.fullName}
              onChange={handleInputChange("fullName")}
            />

            <TextField
              select
              label="State Of Origin"
              variant="outlined"
              fullWidth
              margin="normal"
              value={formData.state}
              onChange={handleInputChange("state")}
            >
              {NigerianStates.map((state, index) => (
                <MenuItem key={index} value={state}>
                  {state}
                </MenuItem>
              ))}
            </TextField>

            <TextField
              label="Mobile Number"
              type="tel"
              variant="outlined"
              fullWidth
              margin="normal"
              value={formData.mobileNumber}
              onChange={handleInputChange("mobileNumber")}
            />

            <TextField
              select
              label="Ticket Type"
              variant="outlined"
              fullWidth
              margin="normal"
              value={formData.ticketType}
              onChange={handleInputChange("ticketType")}
            >
              {Tickets !== null && Tickets.map((ticket, index) => (
                <MenuItem key={index} value={ticket.ticket_type}>
                  {ticket.ticket_type}
                </MenuItem>
              ))}
            </TextField>

            <Button type="submit" variant="contained" color="primary">
              Add
            </Button>
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          {/* <Button onClick={handleClose} autoFocus>
            Agree
          </Button> */}
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}
