import React, { useContext, useEffect, useState } from 'react';
import {
  TextField,
  Button,
  Paper,
  Typography,
} from '@mui/material';
import { styled } from '@mui/material/styles';
import { ContextData } from '../context/Context';
import axios from 'axios';
import { toast } from 'react-toastify';
import baseUrl from '../../Route/BaseURL';

// Styled components
const StyledPaper = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(2),
  textAlign: 'center',
  color: theme.palette.text.secondary,
  width: '80%',
  margin: 'auto',
  marginTop: theme.spacing(5),
}));

const StyledForm = styled('form')(({ theme }) => ({
  '& .MuiTextField-root': {
    margin: theme.spacing(1),
    width: '100%',
  },
}));

const StyledButton = styled(Button)(({ theme }) => ({
  margin: theme.spacing(1),
}));

const UpdatePassword = () => {
  const { ApplicantTokens, accesstoken, setPerson, Person } = useContext(ContextData);

  // State for the form
  const [formData, setFormData] = useState({
    email: '',
    oldPassword: '',
    newPassword: '',
    repeatPassword: '',
  });

  // Fetch data for user details on component mount
  useEffect(() => {
    async function fetchData() {
      try {
        const response = await axios.get(`${baseUrl}/auth/gets_single_admin_user/${ApplicantTokens}`, {
          withCredentials: true,
          headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
            authorization: `Bearer ${accesstoken}`,
          },
        });

        if (response.data && response.data[0]) {

          setPerson(response.data[0]);
          setFormData((prev) => ({
            ...prev,
            email: response.data[0].email || '',
          }));

        }
      } catch (err) {
        console.error(err.message);
      }
    }

    fetchData();
  }, [ApplicantTokens, accesstoken, setPerson]);

  // Handle form field changes
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({ ...prevData, [name]: value.trim() }));
  };

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();

    // Check for empty fields
    if (Object.values(formData).some((value) => value === '')) {
      toast.warn('Please fill in all fields');
      return;
    }

    // Check if new password matches repeat password
    if (formData.newPassword !== formData.repeatPassword) {
      toast.warn('Passwords do not match');
      return;
    }

    // Submit password update request
    const data = { ...formData, token: ApplicantTokens };
    axios
      .post(`${baseUrl}/auth/update_passward`, data, {
        withCredentials: true,
        headers: {
          Accept: 'application/json',
          authorization: `Bearer ${accesstoken}`,
        },
      })
      .then((res) => {
        if (res.data.status) {
          toast.success(res.data.statusText);
        } else {
          toast.warn(res.data.statusText);
        }
      })
      .catch((err) => {
        console.error(err.message);
        toast.error('Something went wrong, please try again.');
      });
  };

  return (
    <div className="flex justify-center items-center" style={{ width: '100%', height: '100%' }}>
      <StyledPaper>
        <Typography variant="h6" gutterBottom>
          Change Password
        </Typography>
        <StyledForm onSubmit={handleSubmit}>
          <TextField
            label="Email"
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
            disabled
          />
          <TextField
            label="Old Password"
            type="password"
            name="oldPassword"
            value={formData.oldPassword}
            onChange={handleChange}
            required
          />
          <TextField
            label="New Password"
            type="password"
            name="newPassword"
            value={formData.newPassword}
            onChange={handleChange}
            required
          />
          <TextField
            label="Repeat New Password"
            type="password"
            name="repeatPassword"
            value={formData.repeatPassword}
            onChange={handleChange}
            required
          />
          <StyledButton variant="contained" color="primary" type="submit">
            Submit
          </StyledButton>
        </StyledForm>
      </StyledPaper>
    </div>
  );
};

export default UpdatePassword;