import { Button } from "@mui/material";
import React, { useState, useEffect } from "react";
import ArrowForwardIcon from "@mui/icons-material/ArrowForward";
import KasuAt20 from "./KasuAt20";

function Hero() {
    const calculateTimeLeft = () => {
        const targetDate = new Date("December 14, 2024 00:00:00");
        const now = new Date();
        const difference = targetDate - now;
    
        let timeLeft = {};
    
        if (difference > 0) {
          timeLeft = {
            days: Math.floor(difference / (1000 * 60 * 60 * 24)),
            hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
            minutes: Math.floor((difference / 1000 / 60) % 60),
            seconds: Math.floor((difference / 1000) % 60),
          };
        }
    
        return timeLeft;
      };
    
      const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());
    
      useEffect(() => {
        const timer = setInterval(() => {
          setTimeLeft(calculateTimeLeft());
        }, 1000);
    
        return () => clearInterval(timer);
      }, []);
  return (
    <div className="hero-section">
       <video className="video-bg" autoPlay loop muted playsInline>
        <source src="assets/bg-v-1.mp4" type="video/mp4" />
        Your browser does not support the video tag.
      </video>
      <div className="nav">
        {/* <p className="logo">MasterMind Tech</p> */}
        <img className="logo" src="https://mmt-ng.com/logo192.png" alt="mmt" />
        <div className="links">
          <ul>
            <li>
              <a href="#">Home</a>
            </li>
            <li>
              <a href="#events">Events</a>
            </li>
          </ul>
        </div>
      </div>
      <div className="main">
        <div className="left">
          <div className="image"><img src="/assets/flyr.jpeg" alt="kasa pic" /></div>
        </div>
        <div className="right">
        
          <h1 className="heading" style={{color:"white"}}>   <KasuAt20/> </h1>
          <div className="countdown">
      <div className="day">
        <p>{timeLeft.days || 0}</p>
        <p>Days</p>
      </div>
      <div className="hour">
        <p>{timeLeft.hours || 0}</p>
        <p>Hours</p>
      </div>
      <div className="minute">
        <p>{timeLeft.minutes || 0}</p>
        <p>Minutes</p>
      </div>
      <div className="second">
        <p>{timeLeft.seconds || 0}</p>
        <p>Seconds</p>
      </div>
    </div>
          <div className="action-btn">
            <Button
              sx={{ backgroundColor: "#fff", color: "#FF4545" }}
              className="Hover-"
              size="large"
              variant="contained"
              endIcon={<ArrowForwardIcon />}
            >
              <a href="#tickets">Get Your Tickets</a> 
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Hero;
