import React, { useContext, useEffect, useState } from 'react';
import { TextField, Button, Radio, FormControlLabel } from '@mui/material';
import axios from 'axios';
import baseUrl from '../../Route/BaseURL';
import { toast } from 'react-toastify';
import { ContextData } from '../context/Context';

const ValidationSection = () => {


  const [selectedOption, setSelectedOption] = useState('validate with ticket ID or mobile number');
  const [inputValue, setInputValue] = useState('');
  const [searchText, setSearchText] = useState("");
  const [Result,setResult] = useState(null)
  const {PersonUser} = useContext(ContextData)

  const handleSubmit = () => {
    console.log(`Submitted ${selectedOption}: ${inputValue}`);
  };

  const handleSearchChange = (e) => {
    setSearchText(e.target.value);
  };



  useEffect(() => {

      let path
    async function FetchData() {
      //  setLoader(true)

        if(PersonUser !== null ){
          
            if( PersonUser.check_in_out === "Check In"){
               path = "validate_au"
            }else{
               path = "validate_au_out"
            }

            
        }
   
      try {
        const response = await axios.get(
          `${baseUrl}/auth/${path}/${searchText}`,
          {
            withCredentials: true,
            headers: {
              Accept: "application/json",
              //'authorization': `Bearer ${accesstoken}`
            },
          }
        );

        if (response.data.status === "new") {
             toast.success(response.data.data)
             setResult(response.data);

        }else if (response.data.status === "not"){
         // toast.warn(response.data.data)
          setResult(response.data);

        } else {
          setResult(null);
        }
        if(response.data.status === "already"){
          toast.warn(response.data.data)
          setResult(response.data);
        }

      } catch (err) {
        if (err) console.log(err.message);
        // setLoader(false)
      }
    }
    FetchData();


  },[searchText]);  



  return (
    <div className="flex items-center justify-center validator-con flex-col">
       <h1 className="text-3xl font-extrabold"> {PersonUser !== null && PersonUser.check_in_out === "Check In" ? "Check In" : "Check Out"}</h1>
      <div className="p-5 v-con-x bg-slate-100 shadow-lg rounded-lg w-1/2">
        <h2 className="text-lg font-semibold mb-4">Validation Options</h2>

        <div className="flex mb-4">
         {/*  <FormControlLabel
            control={
              <Radio
                checked={selectedOption === 'validate with ticket number'}
                onChange={() => setSelectedOption('validate with ticket number')}
                value="validate with ticket number"
                color="primary"
              />
            }
            label="Validate with Ticket Number"
          /> */}

         {/*  <FormControlLabel
            control={
              <Radio
                checked={selectedOption === 'validate with mobile number'}
                onChange={() => setSelectedOption('validate with mobile number')}
                value="validate with mobile number"
                color="primary"
              />
            }
            label="Validate with Mobile Number"
          /> */}
        </div>

        <div className="flex items-center space-x-2">
          <TextField
            label={selectedOption}
            variant="outlined"
            fullWidth
            value={searchText}
            onChange={handleSearchChange}
          />
         {/*  <Button
            variant="contained"
            color="primary"
            onClick={handleSubmit}
          >
            Validate
          </Button> */}
        </div>
        {
          Result !== null && 
        <p className={`text-center font-extrabold  ${Result.status === "new" ? `bg-green-950` : `${Result.status === "already" ? "bg-blue-950" : "bg-red-800" }` } p-3 text-white mt-2`}>{ <> {Result.status === "new" && Result.data } {Result.status === "not" && Result.data } {Result.status === "already" && Result.data } <span> </span> </>}</p>
        }
      </div>
    </div>
  );
};

export default ValidationSection;
