import React from 'react';
import MMT from './MMT'

// Utility to generate a random value within a range
const random = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min;

// Generate an array of watermark styles with different positions, angles, and opacities
const generateWatermarks = (count) => {
  const watermarks = [];
  for (let i = 0; i < count; i++) {
    watermarks.push({
      left: `${random(0, 100)}vw`, // Random horizontal position
      top: `${random(0, 100)}vh`, // Random vertical position
      transform: `rotate(${random(-45, 45)}deg)`, // Random rotation between -45 and 45 degrees
      opacity: random(10, 20) / 100, // Random opacity between 0.1 and 0.2 for watermark effect
    });
  }
  return watermarks;
};

// Main Component
export default function WatermarkBackground() {
  const watermarks = generateWatermarks(10); // Generate 20 watermarks

  return (
    <div className='wtr-con' style={styles.container}>
        <MMT/>
      {watermarks.map((style, index) => (
        <div key={index} style={{ ...styles.watermark, ...style }}>
          MastermindTech
        </div>
      ))}
    </div>
  );
}

// Styles
const styles = {
  container: {
    width: '100%',
    height: '100vh',
    position: 'relative',
    backgroundColor: '#f4f4f4',
    overflow: 'hidden',
  },
  watermark: {
    position: 'absolute',
    fontSize: '3em',
    fontWeight: 'bold',
    color: 'transparent',
    backgroundImage: 'linear-gradient(45deg, darkred, crimson, #b22222, firebrick)',
    backgroundClip: 'text',
    WebkitBackgroundClip: 'text',
    userSelect: 'none',
    whiteSpace: 'nowrap',
    ZIndex:1
  },
};