import React, { useContext, useEffect, useMemo, useState } from "react";
import {
  Avatar,
  Container,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
  Typography,
  IconButton,
  Button,
} from "@mui/material";
import {
  Search as SearchIcon,
  ArrowBack as ArrowBackIcon,
  ArrowForward as ArrowForwardIcon,
} from "@mui/icons-material";
import axios from "axios";
import { ContextData } from "../context/Context";
import baseUrl from "../../Route/BaseURL";
import { toast } from "react-toastify";
import AddAttendeeDialog from "../Dashboard/Attendees/AddAttendeeDialog";
import Tooltipattdss from "./Tooltipattdss";
import usertype from "../../Route/UserType";
/* import EditStudentPop from './PopDialogue/Student/EditStudentPop';
 */

const Attendees = () => {

  const [searchText, setSearchText] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [StudentData, setStudentData] = useState(null);
  const [CountData, setCountData] = useState(0);
  const rowsPerPage = 50;
  const { setLoader, accesstoken, RefreshUesFF,setRefreshUesFF ,Checker} = useContext(ContextData);
 

  useEffect(() => {
    async function FetchData() {
      //  setLoader(true)

      try {
        const response = await axios.get(`${baseUrl}/auth/get_audiance`, {
          withCredentials: true,
          headers: {
            'Accept': "application/json",
           'authorization': `Bearer ${accesstoken}`
          },
        });

        setCountData(Object.keys(response.data));
        setStudentData(response.data);
        setLoader(false);
      } catch (err) {
        if (err) console.log(err.message);
        // setLoader(false)
      }
    }
    FetchData();
  }, [RefreshUesFF]);

  const handleSearchChange = (e) => {
    setSearchText(e.target.value);
  };

  const List = Object.keys(StudentData !== null && StudentData).map(
    (data) => StudentData[data]
  );
  const filteredUsers = List.filter((user) => {
    const lowerCaseSearchText = searchText.toLowerCase().trim();
    return (
      (
      user.fullname.toLowerCase().includes(lowerCaseSearchText)) ||
      user.mobile.toLowerCase().includes(lowerCaseSearchText) ||
      user.stateoforigin.toLowerCase().includes(lowerCaseSearchText) ||
      ( user.ticketType !== null &&  user.ticketType.toLowerCase().includes(lowerCaseSearchText)) ||  
     // user.registr_fee.toLowerCase().includes(lowerCaseSearchText) ||
      user.paid_for.toLowerCase().includes(lowerCaseSearchText) 
      // Add more fields for search if needed (e.g., state, phone number)
    );
  });
  

  const totalPages = Math.ceil(filteredUsers.length / rowsPerPage);
  const indexOfLastUser = currentPage * rowsPerPage;
  const indexOfFirstUser = indexOfLastUser - rowsPerPage;
  const usersToShow = filteredUsers.slice(indexOfFirstUser, indexOfLastUser);
  const count = usersToShow.length
 // const sum = usersToShow.reduce((total, num) => console.log(total.registr_fee + num.registr_fee), 0);


  usersToShow.sort();
  usersToShow.reverse()



  const filteredApplicants = useMemo(() => {

    return StudentData ? StudentData.filter(list => list.ticketType === "stall") : [];

  }, [StudentData]);

  const countallStall = filteredApplicants.length;

  
  const handleNextPage = () => {
    setCurrentPage((prevPage) => prevPage + 1);
  };

  const handlePrevPage = () => {
    setCurrentPage((prevPage) => prevPage - 1);
  };

  const HanldeRemove = async (sttokens) => {

    try {
      const response = await axios.delete(`${baseUrl}/auth/remove_attendee/${sttokens}`,
        {
          withCredentials: true,
          headers: {
             'Accept': "application/json",
           'authorization': `Bearer ${accesstoken}`
          },
        }
      );
      toast.success(response.data)
      setRefreshUesFF(!RefreshUesFF)
    } catch (err) {
      if (err) console.log(err.message);
    }

  };




  


  return (
    <div className="con-dev" style={{ width: "100%", height: "100%" }}>
      <Container sx={{ width: "100%",height: "100%" }}>
        <Typography
          variant="h4"
          gutterBottom
          className="flex justify-between items-center admin-users_header"
         
        >
          <p className="font-extrabold mt-3">Attendees</p>
           <p className="text-base">Total Registered {count.toLocaleString()}</p>
          <div className="admin-users_header-btn-con">
            <AddAttendeeDialog />
          </div>
        </Typography>
 
        <TextField
          label="Search"
          variant="outlined"
          value={searchText}
          onChange={handleSearchChange}
          InputProps={{
            endAdornment: (
              <IconButton>
                <SearchIcon />
              </IconButton>
            ),
          }}
          fullWidth
          margin="normal"
        />
        <div className="admin-users_table-con">
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow sx={{ background: "rgb(0, 24, 69)" }}>
                  <TableCell
                    align="center"
                    sx={{ color: "white", fontWeight: "800" }}
                  >
                    No
                  </TableCell>
                  <TableCell
                    align="center"
                    sx={{ color: "white", fontWeight: "800",whiteSpace:"nowrap" }}
                  >
                    Full Name
                  </TableCell>
                  <TableCell
                    align="center"
                    sx={{ color: "white", fontWeight: "800",whiteSpace:"nowrap" }}
                  >
                    State Of Origin
                  </TableCell>
                  <TableCell
                    align="center"
                    sx={{ color: "white", fontWeight: "800",whiteSpace:"nowrap" }}
                  >
                    Phone No.
                  </TableCell>
                  <TableCell
                    align="center"
                    sx={{ color: "white", fontWeight: "800" ,whiteSpace:"nowrap"}}
                  >
                    Payment Status
                  </TableCell>
                  
                  <TableCell
                    align="center"
                    sx={{ color: "white", fontWeight: "800",whiteSpace:"nowrap" }}
                  >
                     Paid For
                  </TableCell>
                  <TableCell
                    align="center"
                    sx={{ color: "white", fontWeight: "800" ,whiteSpace:"nowrap"}}
                  >
                    Fee
                  </TableCell>

             
                  <TableCell
                    align="center"
                    sx={{ color: "white", fontWeight: "800" ,whiteSpace:"nowrap"}}
                  >
                    Membership Type
                  </TableCell>


                  <TableCell
                    align="center"
                    sx={{ color: "white", fontWeight: "800",whiteSpace:"nowrap" }}
                  >
                    Bussiness Name
                  </TableCell>

                  {
                     Checker === usertype[0] &&

                    <TableCell
                    align="center"
                    sx={{ color: "white", fontWeight: "800",whiteSpace:"nowrap" }}
                    >
                    Remove
                    </TableCell>

                  }

                  
                </TableRow>
              </TableHead>
              <TableBody>
                 {usersToShow.map((user, index) => ( 
                  <TableRow  className="row--">
                    <TableCell  align="center" sx={{ whiteSpace: "nowrap" }}>
                     <span className="row---" > {index + 1} </span> 
                    </TableCell>

                    <TableCell align="center" sx={{ whiteSpace: "nowrap" }}>
                      {user.fullname}
                    </TableCell>
                    <TableCell align="center" sx={{ whiteSpace: "nowrap" }}>
                      {user.stateoforigin}
                    </TableCell>
                    <TableCell align="center" sx={{ whiteSpace: "nowrap" }}>
                      {user.mobile}
                    </TableCell>
                    <TableCell align="center" sx={{ whiteSpace: "nowrap" }}>
                     {user.ownertype} 
                    </TableCell>
                    <TableCell align="center" sx={{ whiteSpace: "nowrap" }}>
                    {user.paid_for === "stall" ? <span className="text-blue-50 bg-blue-950 p-2 rounded-lg font-extrabold">{user.paid_for} </span>  : <span className="text-white bg-green-900 p-2 rounded-lg font-extrabold">{user.paid_for} </span> }   
                    </TableCell>
                    <TableCell align="center" sx={{ whiteSpace: "nowrap" }}>
                    {user.registr_fee.toLocaleString()}
                    </TableCell>
                  
                    <TableCell align="center" sx={{ whiteSpace: "nowrap" }}>
                     {user.ticketType === null ? "Stall" : user.ticketType} 
                    </TableCell> 

                    
                    <TableCell align="center" sx={{ whiteSpace: "nowrap" }}>
                   
                     {user.bussines_name === null ? "Not a Stall" :  <Tooltipattdss text={user.bussines_name !== null && user.bussines_name} />} 
                    </TableCell>

                     {
                         Checker === usertype[0] && 
                    <TableCell align="center" sx={{ whiteSpace: "nowrap" }}>
                     
                     <Button
                          // disabled={true}
                           className="Hover-"
                           onClick={() => HanldeRemove(user.user_token)}
                           elevation={10}
                           sx={{
                             whiteSpace: "nowrap",
                             fontSize: "14px",
                             backgroundColor: "rgb(199, 1, 1)",
                             margin: "9px 0px",
                           }}
                           size="large"
                           variant="contained"
                         >
                           Remove
                        
                     </Button>
  
                     </TableCell>
                     
                     }
 
                    
                  </TableRow>
                ))} 
              </TableBody>
            </Table>
          </TableContainer>
        </div>
        {totalPages > 1 && (
          <div>
            {currentPage > 1 && (
              <Button
                className="Hover-"
                onClick={handlePrevPage}
                elevation={10}
                sx={{
                  whiteSpace: "nowrap",
                  fontSize: "14px",
                  backgroundColor: "rgb(0, 24, 69)",
                  color: "white",
                  margin: "9px 4px",
                }}
                size="large"
                variant="contained"
              >
                <ArrowBackIcon />
              </Button>
            )}
            <span>
              Page {currentPage} of {totalPages}
            </span>
            {currentPage < totalPages && (
              <Button
                className="Hover-"
                onClick={handleNextPage}
                elevation={10}
                sx={{
                  whiteSpace: "nowrap",
                  fontSize: "14px",
                  backgroundColor: "rgb(0, 24, 69)",
                  color: "white",
                  margin: "9px 4px",
                }}
                size="large"
                variant="contained"
              >
                <ArrowForwardIcon />
              </Button>
            )}
          </div>
        )}
      </Container>
    </div>
  );
};

export default Attendees;

