import React, { useContext, useState } from "react";
import PopupDialog from "./PopupDialog";
import baseUrl from "../../Route/BaseURL";
import axios from "axios";
import { ContextData } from "../context/Context";

const TicketCards = () => {




  const [Tickets, setTickets] = useState(null);
  const {accesstoken} = useContext(ContextData)


  React.useEffect(() => {


    async function FetchData() {
       
      try {
        const response = await axios.get(`${baseUrl}/auth/get_ticket`,{
          withCredentials: true,
          headers: {
            'Accept': "application/json",
           'authorization': `Bearer ${accesstoken}`
          },
        });
       
        setTickets(response.data);
      
      } catch (err) {
        if (err) console.log(err.message);
        
      }
    }
    FetchData();


  }, []);


  
  const OnclickMove = () => {

    const targetElement = document.getElementById("move");
    if (targetElement) {
      targetElement.scrollIntoView({ behavior: "smooth" });
    }
    
  };





  return (
    <div className="tickets-con" id="tickets">
      <div className="ticket-cards-container">
        {Tickets !== null && Tickets.map((ticket,id) => (
          <div key={id} onClick={OnclickMove} className="ticket-card">
            <div className="ticket-barcode-left"></div>
            <div className="ticket-content">
              <h1 className="ticket-title uppercase">{ticket.ticket_type}</h1>
              <p className="ticket-date uppercase">Date of Event: {ticket.holdDate}</p>
              <p className="ticket-time uppercase">DOOR OPENS AT {ticket.doorOpen}</p>
              {/* <p className="ticket-welcome">
                Tickets Left: {ticket.limt}
              </p> */}
              <p className="ticket-price">{ticket.price.toLocaleString()}</p>
            </div>
            <div className="ticket-barcode-right"></div>
          </div>
        ))}
      </div>
        
          {
            Tickets !== null && 
        <div id="move" className="book-btn-con">
        <PopupDialog />
        </div>
          }
    
      
    </div>
  );
};

export default TicketCards;
